import React from 'react'
import { connect } from 'react-redux'
import { selectFormularioVeiculo } from '../redux/formulario/formulario.selector'
import { createStructuredSelector } from 'reselect'
import { Row, Col, Form, Input, Select, Radio, DatePicker } from 'antd'
import { handleChange } from '../redux/formulario/formulario.actions'
import  carro  from '../assets/icones/carro.png'
import  moto  from '../assets/icones/moto.png'
import  caminhonete  from '../assets/icones/caminhonete.png'
import  vuc  from '../assets/icones/vuc.png'
import { RadioVeiculo, DateConfig } from '.'
import moment from 'moment'

import './style/style.css';

const { Option } = Select

const ErroMarca = () => <p style={{color: '#e57373', fontSize: '14px'}}>A marca do veículo deve ser informada!</p>
const ErroPrecoSeguro = () => <p style={{color: '#e57373', fontSize: '14px'}}>O preço do seu antigo/atual seguro deve ser informado!</p>
const ErroFabricacao = () => <p style={{color: '#e57373', fontSize: '14px'}}>Você deve informar a fabricação do pneu!</p>
const ErroTipoV = () => <p style={{color: '#e57373', fontSize: '14px'}}>É necessário informar o tipo do veículo!</p>
const ErroAro = () => <p style={{color: '#e57373', fontSize: '14px'}}>Obrigatório informar o aro do veículo!</p>
const ErroTotal = () => <p style={{color: '#e57373', fontSize: '14px'}}>É obrigatório informar o valor total do pneu!</p>
const ErroQtd = () => <p style={{color: '#e57373', fontSize: '14px'}}>É Obrigatório informar a quantidade de pneus!</p>
const ErroPlaca = () => <p style={{color: '#e57373', fontSize: '14px'}}>É Obrigatório informar a placa do seu veículo!</p>

const styles = [
    {
        color: '#212529',
        fontSize: '16px'
    }
]

const Veiculo = ({veiculo, handleChange}) => {
    console.log(veiculo)
    return (
        <>
            <Row gutter={32}>
                <Col md={12}>
                    <Form.Item
                        label={<label style={styles[0]} htmlFor='input-marcaPneu'>MARCA DO PNEU</label>}
                        required
                        extra={ veiculo.errors ? veiculo.errors.marca ? <ErroMarca /> : null : null }
                    >
                        <Input
                            value={veiculo.marca}
                            id='input-marcaPneu'
                            style={{fontFamily: "'Open Sans', sans-serif",}}
                            onChange={e => handleChange(e.target.value, 'MARCA_PNEU')}
                            placeholder='Bridgestone, Pirelli, Goodyear.'
                        />
                    </Form.Item>
                </Col>
                <Col md={12}>
                    <Form.Item
                        label={<label style={styles[0]} htmlFor='input-fabricacao'>FABRICAÇÃO</label>}
                        required
                        extra={ veiculo.errors ? veiculo.errors.fabricacao ? <ErroFabricacao /> : null : null }
                    >
                        <Select
                            style={{ width: '100%', fontFamily: "'Open Sans', sans-serif", }}
                            id='input-fabricacao'
                            placeholder='Selecione a opção de fabricação do seu pneu'
                            onChange={(e) => handleChange(e, 'TIPO_FABRICACAO')}
                            value={veiculo.fabricacao}
                        >
                            <Option value='Nacional'>Nacional</Option>
                            <Option value='Internacional'>Internacional</Option>
                        </Select>
                    </Form.Item>
                </Col>
                <Col md={24}>
                    <Form.Item
                        label={<label style={styles[0]} htmlFor='input-tipoDeVeiculo'>TIPO DE VEÍCULO</label>}
                        required
                        extra={ veiculo.errors ? veiculo.errors.tipo_de_veiculo ? <ErroTipoV /> : null : null }
                    >
                        <Radio.Group
                            onChange={(e) => handleChange(e.target.value, 'TIPO_DE_VEICULO')}
                            value={veiculo.tipo_de_veiculo}
                            id='input-tipoDeVeiculo'
                            style={{width: '100%', textAlign: 'center', fontFamily: "'Open Sans', sans-serif",}}
                        >
                            <Row gutter={[32,32]}>
                                <Col xs={12} lg={6}>
                                    <RadioVeiculo value='car' icone={carro} alt='Carro' label='CARRO' />
                                </Col>
                                <Col xs={12} lg={6}>
                                    <RadioVeiculo value='moto' icone={moto} alt='Moto' label='MOTO' />
                                </Col>
                                <Col xs={12} lg={6}>
                                    <RadioVeiculo value='caminhonete' icone={caminhonete} alt='caminhonete' label='CAMINHONETE' />
                                </Col>
                                <Col xs={12} lg={6}>
                                    <RadioVeiculo value='vuc' icone={vuc} alt='V.U.C' label='V.U.C' />
                                </Col>
                            </Row>
                        </Radio.Group>
                    </Form.Item>
                </Col>
                {
                    veiculo.tipo_de_veiculo === 'car' ?
                        (<Col md={24}>
                            <Form.Item
                                label={<label style={styles[0]} htmlFor='input-aroVeiculo'>QUAL O ARO DO SEU VEÍCULO?</label>}
                                required
                                extra={ veiculo.errors ? veiculo.errors.aro ? <ErroAro /> : null : null }
                            >
                                <Select
                                    style={{ width: '100%', fontFamily: "'Open Sans', sans-serif", }}
                                    id='input-aroVeiculo'
                                    onChange={e => handleChange(e, 'ARO_PNEU')}
                                    value={veiculo.aro}
                                >
                                    <Option value='15'>15</Option>
                                    <Option value='16'>16</Option>
                                    <Option value='17'>17</Option>
                                    <Option value='18'>18</Option>
                                    <Option value='20'>20</Option>
                                    <Option value='22'>22</Option>
                                </Select>
                            </Form.Item>
                        </Col>)
                    : veiculo.tipo_de_veiculo === 'moto' ?
                        (<Col xs={24}>
                            <Form.Item
                                label={<label style={styles[0]} htmlFor='input-aroVeiculo'>QUAL O ARO DO SEU VEÍCULO?</label>}
                                required
                                extra={ veiculo.errors ? veiculo.errors.aro ? <ErroAro /> : null : null }
                            >
                                <Radio.Group
                                    onChange={e => handleChange(e.target.value, 'ARO_PNEU')}
                                    value={veiculo.aro}
                                    style={{fontFamily: "'Open Sans', sans-serif",}}
                                    id='input-aroVeiculo'
                                >
                                    <Radio value='Liga leve'>Liga leve</Radio>
                                    <Radio value='Roda raiada'>Roda raiada</Radio>
                                </Radio.Group>
                            </Form.Item>
                        </Col>)
                    : veiculo.tipo_de_veiculo === 'caminhonete' ?
                        (<Col md={24}>
                            <Form.Item
                                label={<label style={styles[0]} htmlFor='input-aroVeiculo'>QUAL O ARO DO SEU VEÍCULO</label>}
                                required
                                extra={ veiculo.errors ? veiculo.errors.aro ? <ErroAro /> : null : null }
                            >
                                <Select
                                    style={{ width: '100%', fontFamily: "'Open Sans', sans-serif", }}
                                    id='input-aroVeiculo'
                                    onChange={e => handleChange(e, 'ARO_PNEU')}
                                    value={veiculo.aro}
                                >
                                    <Option value='15'>15</Option>
                                    <Option value='17.5'>17.5</Option>
                                    <Option value='16'>16</Option>
                                    <Option value='20'>20</Option>
                                    <Option value='22.5'>22.5</Option>

                                </Select>
                            </Form.Item>
                        </Col>)
                    : veiculo.tipo_de_veiculo === 'vuc' ? 
                        (<Col md={24}>
                            <Form.Item
                                label={<label style={styles[0]} htmlFor='input-aroVeiculo'>QUAL O ARO DO SEU V.U.C?</label>}
                                required
                                extra={ veiculo.errors ? veiculo.errors.aro ? <ErroAro /> : null : null }
                            >
                                <Select
                                    style={{ width: '100%', fontFamily: "'Open Sans', sans-serif" }}
                                    id='input-aroVeiculo'
                                    onChange={e => handleChange(e, 'ARO_PNEU')}
                                    value={veiculo.aro}
                                >
                                    <Option value='15'>15</Option>
                                    <Option value='17.5'>17.5</Option>
                                    <Option value='16'>16</Option>
                                    <Option value='20'>20</Option>
                                    <Option value='22.5'>22.5</Option>

                                </Select>
                            </Form.Item>
                        </Col>)
                    :
                        null
                }
                <Col md={12}>
                    <Form.Item 
                        label={<label style={styles[0]} htmlFor='input-valorTotal'>VALOR TOTAL DOS PNEUS</label>}
                        extra={ veiculo.errors ? veiculo.errors.total ? <ErroTotal /> : null : null }
                    >
                        <Input
                            value={veiculo.total}
                            id='input-valorTotal'
                            style={{fontFamily: "'Open Sans', sans-serif",}}
                            onChange={e => {
                                let valor = e.target.value
                                valor = valor.replace(/\D/g, '')
                                handleChange(valor, 'VALOR_TOTAL_PNEU')
                            }}
                            placeholder='Apenas números inteiros'
                        />
                    </Form.Item>
                </Col>
                <Col md={12}>
                    <Form.Item 
                        label={<label style={styles[0]} htmlFor='input-quantidadePneus'>QUANTIDADE DE PNEUS</label>}
                        required
                        extra={ veiculo.errors ? veiculo.errors.qtd ? <ErroQtd /> : null : null }
                    >
                        <Input
                            value={veiculo.qtd}
                            id='input-quantidadePneus'
                            style={{fontFamily: "'Open Sans', sans-serif"}}
                            onChange={e => {
                                let valor = e.target.value
                                valor = valor.replace(/\D/g, '')
                                handleChange(valor, 'QUANTIDADE_PNEU')
                            }}
                            placeholder='Apenas números inteiros'
                        />
                    </Form.Item>
                </Col>
                <Col md={24}>
                    <Form.Item 
                        label={<label style={styles[0]} htmlFor='input-placaVeiculo'>PLACA DO SEU VEÍCULO</label>}
                        required
                        extra={ veiculo.errors ? veiculo.errors.vehiclePlate ? <ErroPlaca /> : null : null }
                    >
                        <Input
                            id='input-placaVeiculo'
                            style={{fontFamily: "'Open Sans', sans-serif"}}
                            value={veiculo.vehiclePlate}
                            onChange={e => handleChange(e.target.value, 'PLACA_DO_VEICULO')}
                        />
                    </Form.Item>
                </Col>
                <Col md={24}>
                    <h2 style={{
                        textAlign: 'center',
                        marginBottom: '20px',
                        fontWeight: 600,
                        fontSize: '24px'
                    }}>TEM OU TEVE ALGUM SEGURO?</h2>
                    <Radio.Group
                        onChange={(e) => handleChange(e.target.value, 'TEM_SEGURO')}
                        value={veiculo.temSeguro}
                        style={{width: '100%', textAlign: 'center', margin: '0px 0px 20px', fontFamily: "'Open Sans', sans-serif"}}
                    >
                        <Row gutter={[32,32]}>
                            <Col span={12}>
                                <Radio.Button
                                    value={true}
                                    style={{
                                        padding: '10px 0',
                                        width: '100%',
                                        height: 'auto',
                                        textAlign: 'center'
                                    }}
                                >
                                    <div style={{color: '#212529', fontWeight: 600, fontSize: '16px'}}>SIM</div>
                                </Radio.Button>
                            </Col>
                            <Col span={12}>
                                <Radio.Button
                                    value={false}
                                    style={{
                                        padding: '10px 0',
                                        width: '100%',
                                        height: 'auto',
                                        textAlign: 'center',
                                        fontSize: '15px',
                                        fontWeight: 500
                                    }}
                                >
                                    <div style={{color: '#212529', fontWeight: 600, fontSize: '16px'}}>NÃO</div>
                                </Radio.Button>
                            </Col>
                        </Row>
                    </Radio.Group>
                </Col>
                {
                    veiculo.temSeguro ?
                        <>
                            <Col md={24}>
                                <Form.Item 
                                    label={<label style={styles[0]} htmlFor='input-precoSeguro'>QUANTO PAGA / PAGOU PELO SEU ÚLTIMO SEGURO?</label>}
                                    required={veiculo.temSeguro}
                                    extra={ veiculo.errors ? veiculo.errors.precoSeguro ? <ErroPrecoSeguro /> : null : null }
                                >
                                    <Input
                                        value={veiculo.precoSeguro}
                                        id='input-precoSeguro'
                                        style={{fontFamily: "'Open Sans', sans-serif"}}
                                        onChange={e => {
                                            let valor = e.target.value
                                            valor = valor.replace(/\D/g, '')
                                            handleChange(valor, 'PRECO_SEGURO')
                                        }}
                                        placeholder='Apenas números inteiros'
                                    />
                                </Form.Item>
                            </Col>
                            <Col md={24}>
                                <Form.Item 
                                    label={<label style={styles[0]} htmlFor='input-ciaSeguradora'>QUAL CIA SEGURADORA?</label>}
                                    colon={false}
                                    required={veiculo.temSeguro}
                                    extra={ veiculo.errors ? veiculo.errors.ciaSeguradora ? <ErroPrecoSeguro /> : null : null }
                                >
                                    <Input
                                        value={veiculo.ciaSeguradora}
                                        id='input-ciaSeguradora'
                                        style={{fontFamily: "'Open Sans', sans-serif"}}
                                        onChange={e => handleChange(e.target.value, 'CIA_SEGURADORA')}
                                        placeholder='Porto Seguro, HDI, Mapfre...'
                                    />
                                </Form.Item>
                            </Col>
                            <Col md={24}>
                                <Form.Item
                                    label={<label style={styles[0]} htmlFor='input-fimContratoSeguradora'>QUANDO TERMINA SEU CONTRATO?</label>}
                                    colon={false}
                                >
                                    <DatePicker
                                        id='input-fimContratoSeguradora'
                                        value={veiculo.fimContrato ? moment(veiculo.fimContrato) : null}
                                        onChange={(date, dateString) => {
                                            moment(date).isValid() ?
                                                handleChange(moment(date), 'FIM_CONTRATO')
                                            :
                                                handleChange(null, 'FIM_CONTRATO')
                                        }}
                                        placeholder='Selecione a data'
                                        format='DD/MM/YYYY'
                                        style={{width: '100%', fontFamily: "'Open Sans', sans-serif"}}
                                        locale={DateConfig}
                                    />
                                </Form.Item>
                            </Col>
                        </>
                    : null
                }
            </Row>
        </>
    )
}

const mapDispatchToProps = dispatch => ({
    handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
    veiculo: selectFormularioVeiculo
})

export default connect(mapStateToProps, mapDispatchToProps)(Veiculo)
