import React from 'react'
import { Radio, Row, Col } from 'antd'
import { connect } from 'react-redux'
import { handleChange } from '../redux/formulario/formulario.actions'

const RadioButtonOnsurance = ({handleChange, funcao, value}) => {
    return (
        <Radio.Group
            onChange={e => handleChange(e.target.value, funcao)}
            value={value}
            buttonStyle='outline'
            style={{width: '100%'}}
        >
            <div style={{
                display: 'flex'
            }}>
                <Radio.Button
                    value={true}
                    style={{
                        padding: '10px',
                        height: 'auto',
                        width: '100px',
                        textAlign: 'center',
                        fontSize: '18px',
                        fontFamily: "'Open Sans', sans-serif",
                        margin: '0 10px 0 0'
                    }}
                >
                    <div>SIM</div>
                </Radio.Button>
                <Radio.Button 
                    value={false}
                    style={{
                        padding: '10px',
                        width: '100px',
                        height: 'auto',
                        textAlign: 'center',
                        fontSize: '18px',
                        fontFamily: "'Open Sans', sans-serif",
                        margin: '0 0 0 10px'
                    }}
                >
                    <div>NÃO</div>
                </Radio.Button>
            </div>
        </Radio.Group>
    )
}

const mapDispatchToProps = dispatch => ({
    handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

export default connect(null, mapDispatchToProps)(RadioButtonOnsurance);