import React from 'react'
import { connect } from 'react-redux'
import { createStructuredSelector } from 'reselect'
import { selectStep, selectFormulario, SelectLoading } from '../redux/formulario/formulario.selector'
import { Card, Steps, Button, message, Col, Row} from 'antd'
import { irProximoStep, irStepAnterior, handleChange } from '../redux/formulario/formulario.actions'
import { Veiculo, Cotacao, EntendaOnsurnance, SobreVoce, ResultadoFormulario, StepsMobile } from '../components'
import { withBreakpoints } from 'react-breakpoints'

import './style/style.css'

const { Step } = Steps

const steps = [
    {
        title: 'Cotação',
        header: 'Cotação Onsurance',
        content: <Cotacao />
    },
    {
        title: 'Pneu',
        header: 'Dados do pneu',
        content: <Veiculo />
    },
    {
        title: 'Onsurance',
        header: 'Entender seu uso Onsurance',
        content: <EntendaOnsurnance />
    },
    {
        title: 'Sobre você',
        header: 'Conte-nos sobre você',
        content: <SobreVoce />
    },
    {
        title: 'Resultado',
        header: 'Resultado da cotação',
        content: <ResultadoFormulario />
    }
]

const styles = [
    {
        width: '100%',
        heigth: 'auto',
        maxWidth: '900px',
        borderRadius: '10px',
    },
    {
        textAlign: 'center',
        marginBottom: '20px',
        fontWeight: 600,
        fontSize: '24px'
    },
    {
        display: 'block',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '40%',
    }
]

const IndexComponent = ({ step, irProximoStep, irStepAnterior, state, handleChange, loading, currentBreakpoint }) =>  {
    console.log(currentBreakpoint)
    return (
        <div className='conteudo'>
            {
                step === 5 ? 
                    (<ResultadoFormulario />)
                :
                <>
                    <Row>
                        {
                            currentBreakpoint === 'mobile' ||
                            currentBreakpoint === 'mobileLandscape' ?
                                    <StepsMobile step={step + 1} title={steps[step].title} />
                            :
                                <Col>
                                    <Steps current={step} style={{marginBottom: '30px', padding: '0 50px 0 50px'}}>
                                        {steps.map(item => (
                                            <Step key={item.title} title={item.title} />
                                        ))}
                                    </Steps>
                                </Col>
                        }
                    </Row>
                    <div className='centralizar'>
                        <Card 
                            className='card-main'
                            style={styles[0]}
                            id='cardPrincipal'
                            bordered={false}
                            bodyStyle={
                                currentBreakpoint === 'mobile' || currentBreakpoint === 'mobileLandscape' ? 
                                    {padding: '24px 16px'}
                                :
                                    {padding: '24px 80px'}
                            }
                            actions={[
                                <Button 
                                    onClick={irStepAnterior}
                                    disabled={step === 0}
                                    type="link"
                                    id={`buttonAnterior${step}`}
                                    block
                                    style={{fontFamily: "'Open Sans', sans-serif"}}
                                >ANTERIOR</Button>,
                                <Button onClick={async () => {
                                    switch(step){
                                        case 0:
                                            await irProximoStep('PROXIMO_STEP')
                                            break
                                        case 1:
                                            await irProximoStep('PROXIMO_STEP_1')
                                            break
                                        case 2:
                                            await irProximoStep('PROXIMO_STEP_2')
                                            break
                                        default:
                                            handleChange(true, 'SET_LOADING')
                                            const requestOptions = {
                                                method: 'GET',
                                                redirect: 'follow'
                                            }
                                            let telefone = state.usuario.telefone.replace(/\D/g, '')
                                            const params = {
                                                totalValue: Number.parseInt(state.veiculo.total),
                                                qtd: Number.parseInt(state.veiculo.qtd),
                                                vehicleType: state.veiculo.tipo_de_veiculo,
                                                firstName: state.usuario.nome,
                                                lastName: state.usuario.sobreNome,
                                                userEmail: state.usuario.email,
                                                vehiclePlate: state.veiculo.vehiclePlate,
                                                dailyUsage: state.onsurance.horas_por_dia,
                                                phone: telefone
                                            }
                                            const url = `https://api.onsurance.me/quote/tires?totalValue=${params.totalValue}&qtd=${params.qtd}&vehicleType=${params.vehicleType}&firstName=${params.firstName}&lastName=${params.lastName}&userEmail=${params.userEmail}&vehiclePlate=${params.vehiclePlate}&dailyUsage=${params.dailyUsage}&phone=${params.phone}`
                                            await fetch(url, requestOptions)
                                            .then(response => response.json())
                                            .then(result => {
                                                handleChange(false, 'SET_LOADING')
                                                handleChange(result, 'RESULTADO_FINAL')
                                                irProximoStep('PROXIMO_STEP_3')
                                            })
                                            .catch(e => message.error('Houve uma falha na comunicação com o servidor, tente mais tarde.', 3, () => handleChange(false, 'SET_LOADING')))
                                            break
                                    }
                                    let card = document.getElementById('cardPrincipal')
                                    window.scrollTo(0, card.offsetTop)
                                }}
                                style={{fontFamily: "'Open Sans', sans-serif"}}
                                disabled={step === 4}
                                type="link"
                                loading={loading}
                                block
                                id={`buttonProxima${step}`}
                                >PRÓXIMO</Button>
                            ]}
                        >
                            <h2 style={styles[1]}>{steps[step].header.toUpperCase()}</h2>
                            {
                                steps[step].content
                            }
                        </Card>
                    </div>
                </>
            }
        </div>
    )
}

const mapDispatchToProps = dispatch => ({
    irProximoStep: item => dispatch(irProximoStep(item)),
    irStepAnterior: item => dispatch(irStepAnterior(item)),
    handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
    step: selectStep,
    state: selectFormulario,
    loading: SelectLoading
})

export default connect(mapStateToProps, mapDispatchToProps)(withBreakpoints(IndexComponent))