import React from 'react'
import { Row, Col, Form, Input } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMobileAlt, faEnvelope, faMapMarkedAlt } from '@fortawesome/free-solid-svg-icons'
import { selectUsuario } from '../redux/formulario/formulario.selector'
import { connect } from 'react-redux'
import { handleChange } from '../redux/formulario/formulario.actions'
import { createStructuredSelector } from 'reselect'

const ErroNome = () => <p style={{color: '#e57373', fontSize: '14px'}}>É obrigatório informar seu nome!</p>
const ErroSobrenome = () => <p style={{color: '#e57373', fontSize: '14px'}}>É obrigatório informar seu sobrenome!</p>
const ErroCep = () => <p style={{color: '#e57373', fontSize: '14px'}}>É obrigatório informar seu cep!</p>
const ErroPersonalizado = ({msg}) => <p style={{color: '#e57373', fontSize: '14px'}}>{msg}</p>

const styles = [
    {
        color: '#212529',
        fontSize: '16px'
    }
]

const SobreVoce = ({ usuario, handleChange }) => {
    console.log(usuario)
    return (
        <Row gutter={32}>
            <Col xs={24}>
                <div style={{padding: '15px', backgroundColor: '#F7F8FC', marginBottom: '24px'}}>
                    <p style={{margin: '0px', fontSize: '16px', color: '#21253A', fontFamily: "'Open Sans', sans-serif", fontWeight: 400}}>
                        Seu interesse na inovação é o que nos move.
                        Precisamos de algumas informações sobre você para prestar um atendimento melhor.
                    </p>
                </div>
                {/* <h2 style={{marginBottom: '40px', textAlign: 'center', fontSize: 16, color: '#90caf9'}}>Precisamos de algumas informações sobre você para prestar um atendimento melhor.</h2> */}
            </Col>
            <Col md={12}>
                <Form.Item
                    label={<label style={styles[0]} htmlFor='input-nomeUsuario'>QUAL É O SEU NOME?</label>}
                    colon={false}
                    required
                    extra={ usuario.errors ? usuario.errors.nome ? <ErroNome /> : null : null }
                >
                    <Input
                        value={usuario.nome}
                        id='input-nomeUsuario'
                        style={{fontFamily: "'Open Sans', sans-serif"}}
                        onChange={e => {
                            handleChange(e.target.value, 'USUARIO_NOME')
                        }}
                    />
                </Form.Item>
            </Col>
            <Col md={12}>
                <Form.Item
                    label={<label style={styles[0]} htmlFor='input-sobrenomeUsuario'>QUAL É O SEU SOBRENOME</label>}
                    colon={false}
                    required
                    extra={ usuario.errors ? usuario.errors.sobreNome ? <ErroSobrenome /> : null : null }
                >
                    <Input
                        value={usuario.sobreNome}
                        id='input-sobrenomeUsuario'
                        style={{fontFamily: "'Open Sans', sans-serif"}}
                        onChange={e => {
                            handleChange(e.target.value, 'USUARIO_SOBRENOME')
                        }}
                    />
                </Form.Item>
            </Col>
            <Col md={12}>
                <Form.Item
                    label={<label style={styles[0]} htmlFor='input-telefoneUsuario'>TELEFONE</label>}
                    required
                    extra={ usuario.errors ? usuario.errors.telefone ? <ErroPersonalizado msg={usuario.errors.telefone} /> : null : null }
                >
                    <Input
                        prefix={<FontAwesomeIcon icon={faMobileAlt} style={{color: '#1890ff'}} />}
                        value={usuario.telefone}
                        id='input-telefoneUsuario'
                        style={{fontFamily: "'Open Sans', sans-serif"}}
                        onChange={e => {
                            let celular = e.target.value
                            .replace(/\D/g, '')
                            .replace(/(\d{2})(\d)/, '($1) $2')
                            .replace(/(\d{4})(\d)/, '$1-$2')
                            .replace(/(\d{4})-(\d)(\d{4})/, '$1$2-$3')
                            .replace(/(-\d{4})\d+?$/, '$1')
                            handleChange(celular, 'USUARIO_TELEFONE')
                        }}
                    />
                </Form.Item>
            </Col>
            <Col md={12}>
                <Form.Item
                    label={<label style={styles[0]} htmlFor='input-cepUsuario'>CEP</label>}
                    required
                    extra={ usuario.errors ? usuario.errors.cep ? <ErroCep /> : null : null }
                >
                    <Input
                        value={usuario.cep}
                        id='input-cepUsuario'
                        style={{fontFamily: "'Open Sans', sans-serif"}}
                        onChange={e => {
                            let cep = e.target.value
                            .replace(/\D/g, '')
                            .replace(/(\d{5})(\d)/, '$1-$2')
                            .replace(/(-\d{3})\d+?$/, '$1')
                            handleChange(cep, 'USUARIO_CEP')
                        }}
                    />
                </Form.Item>
            </Col>
            <Col md={24}>
                <Form.Item
                    label={<label style={styles[0]} htmlFor='input-emailUsuario'>QUAL É O SEU EMAIL?</label>}
                    colon={false}
                    required
                    hasFeedback
                    extra={ usuario.errors ? usuario.errors.email ? <ErroPersonalizado msg={usuario.errors.email} /> : null : null }
                >
                    <Input
                        prefix={<FontAwesomeIcon icon={faEnvelope} style={{color: '#1890ff'}} />}
                        value={usuario.email}
                        style={{fontFamily: "'Open Sans', sans-serif"}}
                        id='input-emailUsuario'
                        onChange={e => {
                            handleChange(e.target.value, 'USUARIO_EMAIL')
                        }}
                    />
                </Form.Item>
            </Col>
        </Row>
    )
}

const mapDispatchToProps = dispatch => ({
    handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
    usuario: selectUsuario
})

export default connect(mapStateToProps, mapDispatchToProps)(SobreVoce);
